.card {
    position: relative;
  }

.card img {
    display: block;
    border: 2px solid black;
    border-radius: 6px;
    padding: 20px;
}

/* front of card - the picture */
.card .front {
    transform: rotateY(90deg);
    position: absolute;
  }
  .flipped .front {
    transform: rotateY(0deg);
  }

/* back of card - cover */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
  }
  .flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
  }